* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

*:focus {
  outline: none;
}

body.user-is-tabbing *:focus {
  outline: 2px solid #7AACFE;
}

html {
  font-size: var(--grid-height);
}

body {
  position: relative;
  margin: 0 auto;
  background: #fff;
  background: linear-gradient(180deg, rgba(120, 144, 159, 0) 4.17%, rgba(111, 111, 143, 0.3) 52.08%, rgba(120, 144, 159, 0) 100%);
  width: calc(var(--grid-width) * 5);
  height: calc(var(--grid-height) * 9);
  font-family: 'Reem Kufi', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

a {
  color: var(--primary-col);
  transition: color .2s ease-in-out;
  &:hover {
    color: var(--hover-col);
  cursor: pointer;
  }
}

button {
  appearance: none;
  -webkit-appearance: none;
  background: none;
  border: none;
  background: #fff;
  padding: .15rem;
}

canvas {
  position: absolute;
  left: 0;
  top: 0;
  height: 2rem;
  width: 100%;
  z-index: -1;
}

svg {
  fill: var(--primary-col);
    transition: fill .2s ease-in-out;
    &:hover {
      fill: var(--hover-col);
    }
}

section {
  display: grid;
  grid-template-columns: repeat(5, var(--grid-width));
  justify-items: center;
  align-items: center;
}

.back-to-top-button {
  border-radius: 5px;
  box-shadow: var(--shadow);
  cursor: pointer;
  background: #fff;
  font-family: inherit;
  color: var(--primary-col);
  padding: 0 .15rem;
  width: min-content;
  transition: color .2s ease-in-out;
  &:hover {
    color: var(--hover-col);
  }
  &:active {
    box-shadow: var(--inset-shadow);
  }
}